.technical-skills {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 0 60px;
    padding-top: 5.5rem;

    @include md-view {
        flex-direction: column;
        margin: 0px;
        padding-top: 2rem;
    }

    .title {
        color: $main-color;
        margin-bottom: 20px;
    }

    img {
        margin-bottom: 7px;
        position: relative;
        width: 28%;

        &:after {
            background: #000;
            height: 100%;
            width: 100%;
        }

        @include md-view {
            width: 33%;
        }
    }

    p {
        margin: 20px 0;
    }

    & > div {
        flex: 1;
    }
}

.technical__box {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}